<template>
	<view class="single-poster-rate">
		<image src="../../static/svg/star-yellow.svg" v-for="(star) in stars" :key="star" class="star"></image>
		<image src="../../static/svg/star-gray.svg" class="star" v-for="(star) in (5 - stars)" :key="star"></image>
		<view class="score">{{score}}</view>
	</view>
</template>

<script>
	export default {
		name: 'AceRate',
		props: {
			score: Number
		},
		data() {
			return {

			}
		},
		computed: {
			stars(){
				const stars = Math.floor(this.score / 2)
				return stars * 1
			}
		}
	}
</script>
